import { useState } from "react";

export default function PersonalDetails({ user }) {
  const [selectedFile, setSelectedFile] = useState(null);

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
    console.log("Selected file:", file);
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      alert("Please select a file.");
      return;
    }

    const formData = new FormData();
    formData.append("profileImage", selectedFile);
    formData.append("userId", user._id);

    try {
      const res = await fetch("/api/uploadProfileImage", {
        method: "POST",
        body: formData,
      });

      const data = await res.json();
      console.log("Upload response:", data);

      if (res.ok) {
        alert("Image uploaded successfully!");
        window.location.reload(); // to reflect updated image
      } else {
        alert(`Upload failed: ${data.error || "Unknown error"}`);
      }
    } catch (err) {
      console.error("Upload error:", err);
      alert("Upload failed. Check console.");
    }
  };

  return (
    <form className="space-y-4 p-4 max-w-xl mx-auto">
      <h2 className="text-2xl font-bold text-blue-700 mb-4">Personal Information</h2>

      {/* Profile Image Preview */}
      <img
        src={
          selectedFile
            ? URL.createObjectURL(selectedFile)
            : user?.profileImage || "/default-profile.png"
        }
        className="w-32 h-32 rounded-full border object-cover mb-2"
        alt="Profile"
      />

      {/* File Input */}
      <input
        type="file"
        accept="image/*"
        onChange={handleImageChange}
        className="block mb-2"
      />

      {/* Upload Button */}
      <button
        type="button"
        onClick={handleUpload}
        className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
      >
        Upload Profile Image
      </button>

      {/* User Details */}
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mt-6">
        <div>
          <label className="block text-sm font-medium">Name</label>
          <input
            type="text"
            value={user?.name || ""}
            readOnly
            className="w-full p-2 border rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Email</label>
          <input
            type="email"
            value={user?.email || ""}
            readOnly
            className="w-full p-2 border rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Phone</label>
          <input
            type="text"
            value={user?.phone || ""}
            readOnly
            className="w-full p-2 border rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Experience Level</label>
          <input
            type="text"
            value={user?.experienceLevel || ""}
            readOnly
            className="w-full p-2 border rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Work Status</label>
          <input
            type="text"
            value={user?.workStatus || ""}
            readOnly
            className="w-full p-2 border rounded"
          />
        </div>
      </div>
    </form>
  );
}
